#root {
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.dark-mode {
  background-color: linear-gradient(100.72deg, #0098ee 0.05%, #01e5bb 99.98%);
  color: white;
  text-decoration: none;
}

nav {
  width: 84%;
  height: 60px;
  border: 2px solid rgb(255, 255, 255);
  padding: 5px 10px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logoIMG {
  width: 55px;
  height: 55px;
}

.rightNAV {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navCells {
  margin: 10px;
  color: white;
  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  /* text-fill-color: transparent; */
}

.githubIMG {
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;

  /* background: linear-gradient(136.13deg, #0098ee 0%, #00eeb5 104.77%); */
}

.togglerImage,
.toggler {
  background-color: transparent;
  border: none;
  width: 35px;
  height: 35px;
}

.togglerImage:active {
  background-color: #001060;
}

.snapInfo {
  border: 1px solid white;

  width: 75%;
  height: 90px;
  margin-bottom: 20px;

  display: flex;
  justify-content: space-evenly;
}

.snapInfoBox {
  width: 150px;
  height: 70px;
  padding: 10px;

  display: flex;
  flex-direction: column;

  background: linear-gradient(
    95.43deg,
    #0098ee 0.02%,
    #00eeb5 196.57%,
    #00eeb5 196.57%
  );
  border-radius: 4px;
}

.snapInfoHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.headerText {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, "Roboto";
  font-weight: 700;
  font-size: 12px;
}

.headerImg {
  width: 24px;
  height: 24px;
}

.Casesnumbers {
  margin: 0;
  font-size: 25px;
  font-weight: 700;
}

.Date {
  margin: 0;
  font-size: 9px;
  font-weight: 700;
}

.fnNav {
  width: calc(80% - 20px);
  height: 38px;
  padding: 5px 20px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  background: linear-gradient(
    95.43deg,
    #0098ee 0.02%,
    #00eeb5 196.57%,
    #00eeb5 196.57%
  );
  border-radius: 4px 4px 0px 0px;
}

.Navigator {
  font-weight: 700;
  font-size: 15px;

  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  text-decoration: none;

  color: #fff;

  width: auto;
  height: 22px;
  padding: 5px 10px;
  border-radius: 3px;
}

.Navigator:hover {
  background-color: aliceblue;
  color: #0199ee;

  transition: all 0.5s;
  transition-timing-function: ease-in;
}

.searchBar {
  width: 295px;
  height: 30px;
  background-color: transparent;

  display: flex;
  align-items: center;
  flex-direction: row;
}

.searchBox {
  border: none;
  outline: none;

  color: #fff;
  background: linear-gradient(
    90.2deg,
    rgba(0, 96, 150, 0.5) 0%,
    rgba(0, 87, 66, 0.205) 100%
  );
  border-radius: 3px 0 0 3px;

  width: 82%;
  height: 100%;
  padding: 0 3%;
}

.icon {
  width: 22px;
  height: 22px;
}
.searchIcon {
  width: 15%;
  height: 100%;
  border-radius: 0 3px 3px 0;
  background-color: rgba(0, 87, 67, 0.218);
  border: none;
}

.infoCardContainer {
  width: calc(80% + 15px);
  min-height: 400px;
  height: auto;
  padding: 5px;

  background: #191c29;
  box-shadow: 1px 1px 5px 2px rgba(0, 152, 238, 0.15);
  border-radius: 0px 0px 4px 4px;

  display: flex;
  justify-content: center;
  align-content: flex-start;
  flex-wrap: wrap;
}

.infoCard {
  width: 170px;
  height: 125px;
  margin: 8px;
  padding: 10px;

  background: #191c29;
  box-shadow: 1px 1px 5px 2px rgba(0, 152, 238, 0.15);
  border-radius: 4px;
}

.head {
  display: flex;
  flex-direction: row;
}

.flag {
  width: 35px;
  height: 24px;
  margin-top: 20px;
  border-radius: 1px 3px 3px 1px;

}

.summaryInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 5px;
}

.Countryname {
  font-size: 10px;
  font-weight: 600;

  background: linear-gradient(92.48deg, #27a9f5 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.totalCase {
  font-size: 15px;
  font-weight: 600;

  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.totalPatients {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.virus {
  width: 15px;
  height: 15px;
  border-radius: 3px;
  margin-right: 2px;
}

.totalPatientsNum {
  font-size: 17px;
  font-weight: 700;

  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.detailInfo {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.vaccinationInfo {
  width: 45%;
  height: 25px;
  margin: 2px;

  display: flex;
  align-items: center;

  /* background-color: #0097ee71; */
}

.infoDate {
  /* margin: 0; */
  font-size: 9px;
  font-weight: 700;

  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/*
      info Details Start
*/
.bedIcon,
.newCaseIcon,
.coffinIcon,
.hhIcon {
  width: 18px;
  height: 21px;

}

.numContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 3px;

}

.Title {
  font-weight: 700;
  font-size: 7px;
  display: flex;
  align-items: flex-start;

  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.infoNumbers {
  font-weight: 700;
  font-size: 7.5px;
  display: flex;
  align-items: flex-start;

  background: linear-gradient(92.48deg, #0199ee 2.11%, #01e5bb 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/*
      info Details Start
*/
